<template>
  <metainfo>
  </metainfo>
  <div class="grid h-full">
    <div class="col-12 flex justify-content-center">
      <h1>Zamorak Spec Visualizer</h1>
    </div>
    <div class="col-12 flex justify-content-center">
      <Chart type="pie" :data="chartData" :options="chartOptions" :plugins=[ChartDataLabels] class="md:w-full xl:w-5" />
    </div>
  </div>
  <div class="card flex flex-wrap gap-3  justify-content-center">
    <Button @click="displayPhase = 1; currentSpec = 0" label="Reset"/>
    <h4>Phase {{ displayPhase }}</h4>
    <Button @click="nextSpec" label="Next Spec"/>
    <Button v-if="displayPhase < 6" @click="nextPhase" label="Next Phase"/>
  </div>
</template>

<script setup>
import Button from 'primevue/button'
import Chart from 'primevue/chart'
import ChartDataLabels from 'chartjs-plugin-datalabels'
import { ref, computed } from 'vue'
import { useMeta } from 'vue-meta'

const Phases = {
  OneSix: 0,
  Two: 1,
  Three: 2,
  Four: 3,
  Five: 4
}

useMeta({
  title: 'Zamorak Spec Visualizer',
  og: {
    type: 'website',
    title: 'Zamorak Spec Visualizer',
    description: 'Visualize how the Zamorak special attack order works to easily memorize the rotation.',
    image: 'https://i.postimg.cc/900rMZd1/image.png'
  },
  htmlAttrs: { lang: 'en', amp: true }
})

const ACTIVE = '#eab308'
const CURRENT_ACTIVE = '#22c55e'
const CURRENT_INACTIVE = '#ff3d32'
const INACTIVE = 'black'

const displayPhase = ref(1)
const currentPhase = computed(() => {
  const currentPhase = displayPhase.value - 1
  return currentPhase < 5 ? currentPhase : 0
})
const currentSpec = ref(0)

const specs = computed(() => {
  const specs = [
    activeSpecs.value.includes(Phases.OneSix) ? ACTIVE : INACTIVE,
    activeSpecs.value.includes(Phases.Two) ? ACTIVE : INACTIVE,
    activeSpecs.value.includes(Phases.Three) ? ACTIVE : INACTIVE,
    activeSpecs.value.includes(Phases.Four) ? ACTIVE : INACTIVE,
    activeSpecs.value.includes(Phases.Five) ? ACTIVE : INACTIVE
  ]

  if (specs[currentSpec.value] === ACTIVE) {
    specs[currentSpec.value] = CURRENT_ACTIVE
  } else {
    specs[currentSpec.value] = CURRENT_INACTIVE
    specs[getPriorPhase(currentPhase.value)] = CURRENT_ACTIVE
  }
  return specs
})

const activeSpecs = computed(() => [getPriorPhase(currentPhase.value), currentPhase.value, getNextPhase(currentPhase.value)])

function getPriorPhase (phase) {
  if (phase === Phases.OneSix) {
    return Phases.Five
  }

  return phase - 1
}

function getNextPhase (phase) {
  if (phase === Phases.Five) {
    return 0
  }

  return phase + 1
}

function nextSpec () {
  console.log(specs.value)
  for (let i = 0; i < 5; i++) {
    console.log(i, 'spec:', specs.value[i], CURRENT_ACTIVE, specs.value, currentSpec.value)
    if (specs.value[i] === CURRENT_ACTIVE) {
      currentSpec.value = i + 1
      break
    }
  }
  if (currentSpec.value > 4) {
    console.log('there')
    currentSpec.value = 0
  }
}

function nextPhase () {
  displayPhase.value++
}

const chartOptions = ref({
  plugins: {
    legend: {
      labels: {
        font: function (context) {
          var width = context.chart.width
          var size = Math.round(width / 32)

          return {
            weight: 'bold',
            size: size
          }
        },
        generateLabels (chart) {
          const meta = chart.getDatasetMeta(0)
          const style = meta.controller.getStyle(0)
          return [{
            text: 'Next Spec',
            fillStyle: '#22c55e',
            strokeStyle: style.borderColor,
            lineWidth: style.borderWidth
          }, {
            text: 'Other Active Specs',
            fillStyle: '#eab308',
            strokeStyle: style.borderColor,
            lineWidth: style.borderWidth
          }, {
            text: 'Next Spec (Inactive)',
            fillStyle: '#ff3d32',
            strokeStyle: style.borderColor,
            lineWidth: style.borderWidth
          }]
        }
      }
    },
    tooltip: {
      enabled: false
    },
    datalabels: {
      formatter: function (value, context) {
        return context.chart.data.labels[
          context.dataIndex
        ]
      },
      font: function (context) {
        var width = context.chart.width
        var size = Math.round(width / 38)

        return {
          weight: 'bold',
          size: size
        }
      }
    }
  },
  animation: false,
  responseive: false
})

const chartData = computed(() => {
  return {
    labels: ['Flames of Zamorak', 'Infernal Tomb', 'Adrenaline Cage', 'Chaos Blast', 'Rune of Destruction'],
    datasets: [
      {
        data: [20, 20, 20, 20, 20],
        backgroundColor: specs.value
      }
    ]
  }
})
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html {
  height: 100%;
}
</style>
